.filter {
}

.filter__title {
	margin-bottom: 10px;
	font-size: 14px;
}

.filter__body--price {
	display: flex;
	gap: 5px;
	align-items: center;
}

.filter__body--range {
	padding: 30px 0 30px;
}

.filter__body--checkbox-list {
	display: grid;
	gap: 10px;
}

.filter__body-colors-list {
	display: flex;
	gap: 10px;
	flex-wrap: wrap;
}

/* Gradient Range Slider */
#range-slider-gradient {
	/* height: 24px; */
	height: 4px;
	border-radius: 12px;
	background: #d2d4ea;
}

#range-slider-gradient .range-slider__thumb {
	border: 4px solid #fff;
	box-sizing: border-box;
	box-shadow: 0px 0px 20px 0px #d0d5e6;
}

#range-slider-gradient .range-slider__thumb[data-lower] {
	background: #0073e6;
}

#range-slider-gradient .range-slider__thumb[data-upper] {
	background: #ee2c2c;
}

#range-slider-gradient .range-slider__range {
	background: linear-gradient(to right, #0073e6, #ee2c2c);
	background-size: 200% 100%;
	background-position: 50% 0;
}

#range-slider-gradient .range-slider__range[data-active],
#range-slider-gradient
	.range-slider__thumb[data-active]
	~ .range-slider__range {
	animation: move-bg 0.75s infinite linear;
}

@keyframes move-bg {
	0% {
		background-position: 50% 0;
	}

	25% {
		background-position: 100% 0;
	}

	50% {
		background-position: 50% 0;
	}

	75% {
		background-position: 0% 0;
	}

	100% {
		background-position: 50% 0;
	}
}
