@import url(https://fonts.googleapis.com/css?family=Manrope:200,300,regular,500,600,700,800);

* {
	box-sizing: border-box;
}

:root {
	--font-family:  Manrope, sans-serif;
}

body {
	margin: 0;
	font-family: Manrope, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.container {
	width: 100%;
	max-width: 1090px;
	margin: 0 auto;
	padding: 0 15px;
}

.sidebar {
	display: flex;
	flex-direction: column;
	gap: 40px;
}

.widget {
}

.widget__title {
	margin-bottom: 20px;
	font-size: 16px;
	font-weight: 700;
}

.widget__body {

}

.widget__body--filters-list {
	display: grid;
	gap: 20px;
}

label {
	cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}